<template>
<div class="partners" :class="view == 'structure' ? 'partners-structure' : 'partners-list'">
  <div class="container partners-container">
    <!-- <p class="title">{{$localizeFilter('my_partners')}}</p> -->
    <!-- <div class="text-block">
      <p class="text">{{$localizeFilter('total_turnover') + '\n'}}<span style="font-weight: bold;">{{totalTurnover + ' ' + '[COIN]'}}</span></p>
      <p class="text right">{{$localizeFilter('structure_turnover') + '\n'}}<span style="font-weight: bold;">{{structureTurnover + ' ' + '[COIN]'}}</span></p>
    </div> -->

    <div class="partners-title">{{$localizeFilter('my_partners')}}</div>
    <!-- <div class="flex-wrap">
        <div class="number-struct">{{$localizeFilter('partnerts-struct')}} <span class="bold">20</span></div>
        <div class="number-struct">{{$localizeFilter('partners-invited')}} <span class="bold">9</span></div>
    </div>
    <div class="partners-subtitle">{{$localizeFilter('partners-transfer')}}</div>
    <div class="transfer-referrals-block">
      <TransferRefferal v-for="(data, idx) in transferReferralsData" :key="idx" :type="data.type" :data="data" @buyReferral="buyReferral" @cancelSellingReferral="cancelSellingReferral"/>
    </div> -->

    <div class="top-block">
       <Toggle :class="'toggle-block'" :title="$localizeFilter('view') + ':'" :variants="views" @toggle="toggle"/>
       <div class="search" :style="view=='structure' ? 'visibility:hidden;' : ''">
         <p class="search-title">{{$localizeFilter('search') + ':'}}</p>
         <input class="search-field" v-model="searchString" v-maska="'@XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX'" :placeholder="$localizeFilter('telegram_nickname')">
         <button class="basic-button search-button" @click="startSearch()">{{$localizeFilter('search_button')}}</button>
       </div>
    </div>
    <div v-if="view=='structure'">
          <Tree v-if="structure != null && structure != undefined"
            class="tree"
            v-bind:nodes="structure"
            :indentSize="50"
            @sellReferral="sellReferral"/>
    </div>
    <div v-else class="list">
      <div class="list-level" v-for="(level, i) in visibleList" v-bind:key="i">
          <div class="list-partner" v-for="(partner, j) in level" v-bind:key="j">
            <div class="cell-1">{{ $localizeFilter('level') + ' ' + (i+1) }}</div>
            <div class="cell-2">{{partner.email}}</div>
            <div class="cell-3">{{partner.name}}</div>
            <div class="cell-4"><a v-if="partner.telegramName" target="_blank" class="link" :href="'https://t.me/' + partner.telegramName">{{ '@' + partner.telegramName }}</a></div>
            <!-- <button class="sell-referral-button" v-if="i===0" @click="sellReferral(partner)">{{$localizeFilter('sell')}}</button> -->
          </div>
      </div>
    </div>
</div> 
<ChoosePaymentMethodModal v-if="showChoosePaymentMethodModal && paymentMethods" :typeOverride="'crypto'" :paymentMethodsOverride="paymentMethods" :cellSizeOverride="{width: 161, height: 146}" @paymentMethodSelected="paymentMethodSelected" @closeModal="closeModals"/>
</div>
</template>

<script>
import Tree from '@/components/tree/components/Tree.vue';
import Toggle from "@/components/Toggle.vue"
import ChoosePaymentMethodModal from '@/components/newModals/ChoosePaymentMethodModal'
import TransferRefferal from '../components/TransferRefferal.vue'

export default {
  components: {
    Tree,
    Toggle,
    ChoosePaymentMethodModal,
    TransferRefferal
  },
  data() { 
    return {
      searchString: '',
      search: null,
      structure: null,
      list: [],
      totalTurnover: 0,
      structureTurnover: 0,
      views: ['structure', 'list'],
      view: 'structure',
      showChoosePaymentMethodModal: false,
    }
  },
  // watch: {
  //   searchString(newVal, oldVal) {
  //     if (newVal === '') {
  //       this.$nextTick(()=>{
  //         this.search = null
  //       })
  //     }
      
  //   }
  // },
  computed: {
    visibleList() {
      const list = JSON.parse(JSON.stringify(this.list))
      if (this.search == null) {
        return list
      } else {
        const result = []
        for (let i=0; i<list.length; i++) {
          let level = list[i]
          let levelResult = []
          for (let j=0; j<level.length; j++) {
            // // console.log(level[j].telegramName, this.search, level[j].telegramName.startsWith(this.search))
            if ((level[j].telegramName || '').startsWith(this.search)) {
              levelResult.push(level[j])
            }
          }
          if (levelResult.length > 0) { result.push(levelResult) }
        }
        // // console.log(result)
        return result
      }
    },
    paymentMethods() {
      return this.$store.getters.transferReferralsPaymentMethods
    },
    transferReferralsData() {
      return this.$store.getters.transferReferralsData || []
    }
  },
  methods: {
    async sellReferral(partner) {
      const data = JSON.parse(JSON.stringify(partner))
      await this.$store.dispatch('sellReferral', {data})
    },
    async buyReferral(data) {
      await this.$store.dispatch('setBuyReferralPaymentData', {data})
      this.showChoosePaymentMethodModal = true
    },
    async cancelSellingReferral(data) {
      await this.$store.dispatch('cancelSellingReferral', {data})
    },
    async paymentMethodSelected(paymentMethod) {
      await this.$store.dispatch('setBuyReferralPaymentMethod', {paymentMethod})
      await this.$store.dispatch('buyReferral')
    },
    closeModals() {
      this.showChoosePaymentMethodModal = false
      this.clearAll()
    },
    async clearAll() {
      await this.$store.dispatch('clearBuyReferralPaymentData')
    },
    startSearch() {
      this.search = this.searchString.slice(1)
      // // console.log(this.search)
      this.$forceUpdate()
    }, 
    toggle(index) {
      this.view = this.views[index]
    },
    async loadData() {
      if (this.view=='structure') {
         try {
           const data = await this.$store.dispatch('getStructure')
           await this.$store.dispatch('getTransferReferralsPaymentMethods')
           await this.$store.dispatch('getTransferReferralsData')
        //   const node = JSON.parse(JSON.stringify(_structure))
        //   _structure['nodes']=[JSON.parse(JSON.stringify(node)), JSON.parse(JSON.stringify(node))]
        //  _structure['nodes'][0]['nodes']= [JSON.parse(JSON.stringify(node))]
        //  _structure['nodes'][0]['nodes'][0]['nodes']= [JSON.parse(JSON.stringify(node))]
          this.list = data.list
          this.structure = [data.structure]
          this.totalTurnover = data.totalTurnover
          this.structureTurnover = data.structureTurnover
        }
        catch (e) {
          this.$error(e)
        }  
      }
      // else {
      //   // get list ...
      // }
    }
  },
  async mounted() {
    await this.loadData()
  }  
};
</script>

